import image from "../../assets/img/image_2.png";

import Banner from "../common/Banner/Banner";
import Container from "../common/Container/Container";
import s from "./Information.module.css";
import { useTranslation } from 'react-i18next';
import Button from "../common/Button/Button";

const Information = (props) => {
    const { t } = useTranslation();
    return(
        <section className={s.information}>
            <Container>
                <Banner color="white">
                    <div className={s.wrapper}>
                        <div className={s.info}>
                            <h1 className={s.title}>{t("information-title")}</h1>
                            <h2 className={s.subtitle}>{t("information-subtitle")}</h2>
                            <Button class="information" bgColor="transparent" >+998 71 200 0548</Button>
                        </div>
                        <div className={s.img}>
                            <img src={image} alt="image" />
                        </div>
                    </div>
                </Banner>
            </Container>
        </section>
    )
}

export default Information;