import close from "../../../assets/icons/close-grey.svg";

import classNames from "classnames";
import { Transition } from "react-transition-group";
import s from "./Canvas.module.css";

const Canvas = (props) => {
    const classGenerator = (state) => {
        if(state === "entering") return "animate__animated animate__fadeIn"
        else if(state === "exiting") return "animate__animated animate__fadeOut"
    }
    
    const onClick = (e) => {
        if(e.target.classList.contains(s.canvas)){
            props.setTypePopup("");
        }
    }
    return(
        <Transition
        in={props.isShow}
        timeout={1000}
        mountOnEnter
        unmountOnExit
        >
            {state => (
                <div ref={props.canvas} onClick={ onClick } className={classNames(s.canvas, classGenerator(state), {[s.showHeader]: props.isShowHeader, [s.center]: props.isCenter})}>
                    <div className={classNames(s.popup, {[s.active]: props.isShow})}>
                        {
                            props.isCenter &&
                            <div className={s.head}>
                                <button onClick={ () => { props.setTypePopup(""); } } className={s.img}><img src={close} alt="close" /></button>
                            </div>
                        }
                        {props.children}
                    </div>
                </div>
            )}
        </Transition>
    )
}

export default Canvas;