import { useEffect } from 'react';
import { connect } from 'react-redux';
import { useParams } from "react-router-dom";
import { compose } from 'redux';
import { getSearchGoods } from '../../redux/reducers/search-reducer';
import { getActive, getCatalog } from '../../redux/reducers/catalog-reducer';
import Search from './Search';

const SearchAPI = (props) => {
    useEffect(() => {
        props.getSearchGoods();
        props.getCatalog();
    }, [])
    useEffect(() => {
        props.getActive(props.params.selector);
    }, [props.params.selector])
    return <Search {...props} />
}

const withRouter = WrappedComponent => props => {
    const params = useParams();
    return (
        <WrappedComponent
            {...props}
            params={params}
        />
    );
};

const mapStateToProps = (state) => ({
    goods: state.searchReducer.goods,
    catalog: state.catalogReducer.catalog,
    listActive: state.catalogReducer.listActive,
    itemActive: state.catalogReducer.itemActive,
    hrefActive: state.catalogReducer.hrefActive
})

export default compose(
    connect(mapStateToProps, { getSearchGoods, getCatalog, getActive }),
    withRouter,
)(SearchAPI);