import Container from "../common/Container/Container";
import s from "./Goods.module.css";
import classNames from "classnames";
import CardContainer from "../Card/CardContainer";
import { useTranslation } from 'react-i18next';
import Buuton from "../common/Button/Button";
import { useEffect, useState } from "react";

const Goods = (props) => {
    const { t } = useTranslation();
    const [list, setList] = useState([])
    const goods = list.map((item, i) => (
        <li key={i + 1}>
            <CardContainer {...item} />
        </li>
    ))

    useEffect(() => {
        setList(props.goods)
    }, [props.active, props.goods]);

    const onCick = (id) => {
        props.setActive(id)
        setList([]);
    }

    return(
        <section className={s.goods}>
            <Container>
                <nav className={s.nav}>
                    <button onClick={ () => { onCick(1) } } className={classNames(s.item, {[s.active]: props.active === 1})}>{t("goods-button-1")}</button>
                    <button onClick={ () => { onCick(2) } } className={classNames(s.item, {[s.active]: props.active === 2})}>{t("goods-button-2")}</button>
                    <button onClick={ () => { onCick(3) } } className={classNames(s.item, {[s.active]: props.active === 3})}>{t("goods-button-3")}</button>
                </nav>
                {
                    (props.goods.length !== 0) ? 
                    <ul className={s.list}>{goods}</ul>:
                    <div className={s.text}>{t("nothing")}</div>
                }
                {
                    (goods.length !== 0) && <Buuton bgColor="gray" class="showMore">{t("show-more")} 20</Buuton>
                }
            </Container>
        </section>
    )
}

export default Goods;