import empty from "../../assets/img/empty-favourite.png";

import Container from "../common/Container/Container";
import Information from "../Information/Information";
import Navigation from "../Navigation/Navigation";
import { NavLink } from "react-router-dom";
import s from "./Favourite.module.css";
import CardContainer from "../Card/CardContainer";
import { useTranslation } from 'react-i18next';
import Sort from "../common/Sort/Sort";
import Button from "../common/Button/Button";

const Favourite = (props) => {
    const goods = props.goods.map((item, i) => <CardContainer key={i + 1} {...item} />);
    const { t } = useTranslation();
    return(
        <>
            <Navigation list={[
                {
                    text: t("favourites"),
                    href: "favourite"
                }
            ]} />
            <section className={s.basket}>
                <Container>
                    {
                        props.goods.length != 0 ?
                        <>
                            <div className={s.info}>
                                <h1 className={s.heading}>{t("favourites")}</h1>
                                <Sort />
                            </div>
                            <div className={s.list}>{goods}</div>
                        </>:
                        <>
                            <h1 className={s.heading}>{t("favourites")}</h1>
                            <div className={s.wrap}>
                                <div>
                                    <img src={empty} alt="empty" />
                                </div>
                                <div className={s.desc}>
                                    <h2 className={s.title}>{t("favourite-title")}</h2>
                                    <h3 className={s.subtitle}>{t("favourite-subtitle")}</h3>
                                </div>
                                <NavLink to="/">
                                    <Button class="back">{t("to-main")}</Button>
                                </NavLink>
                            </div>
                        </>
                    }
                </Container>
            </section>
            {props.goods.length != 0 && <Information />}
        </>
    )
}

export default Favourite;