import arrow from "../../../assets/icons/arrow/arrow_bottom.svg";

import { useState } from "react";
import s from "./Sort.module.css";
import classNames from "classnames";
import { useTranslation } from 'react-i18next';

const Sort = (props) => {
    const [isActive, setActive] = useState(false);
    const [sort, setSort] = useState("popular");
    const { t } = useTranslation();
    return(
        <div className={s.sort}>
            <div className={s.caption}>{t("sorting")}</div>
            <div className={s.wrap}>
                <button onClick={ () => { setActive(!isActive) } } className={classNames(s.button, {[s.active]: isActive})}>
                    <div className={s.text}>
                        {
                            sort === "popular" ? t("sorting-popular"):
                            sort === "cheaper" ? t("sorting-cheaper"):
                            sort === "expensive" ? t("sorting-expensive"): ""
                        }
                    </div>
                    <div className={s.img}>
                        <img src={arrow} alt="arrow" />
                    </div>
                </button>
                <ul className={classNames(s.items, {[s.active]: isActive})}>
                    <li>
                        <button onClick={ () => {setSort("popular")} } className={classNames(s.item, {[s.active]: sort === "popular"})}>{t("sorting-popular")}</button>
                    </li>
                    <li>
                        <button onClick={ () => {setSort("cheaper")} } className={classNames(s.item, {[s.active]: sort === "cheaper"})}>{t("sorting-cheaper")}</button>
                    </li>
                    <li>
                        <button onClick={ () => {setSort("expensive")} } className={classNames(s.item, {[s.active]: sort === "expensive"})}>{t("sorting-expensive")}</button>
                    </li>
                </ul>
            </div>
        </div>
    )
}

export default Sort;