import empty from "../../assets/img/empty-basket.png";

import Container from "../common/Container/Container";
import Information from "../Information/Information";
import Navigation from "../Navigation/Navigation";
import s from "./Basket.module.css";
import Item from "./Item/Item";
import { NavLink } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import Button from "../common/Button/Button";

const Basket = (props) => {
    const { t } = useTranslation();
    const list = props.goods.map((item, i) => <Item key={i + 1} setNumber={props.setNumber} setActiveGood={props.setActiveGood} setBuy={props.setBuy} {...item} />);
    return(
        <>
            <Navigation list={[
                {
                    text: t("basket"),
                    href: "basket"
                }
            ]} />
            <section className={s.basket}>
                <Container>
                <h1 className={s.heading}>{t("basket")}</h1>
                    {
                        props.goods.length != 0 ?
                        <div className={s.wrapper}>
                            <div className={s.list}>
                                <div className={s.btns}>
                                    <button className={s.btn}></button>
                                    <button onClick={ () => { props.resetBuy() } } className={s.del}>{t("basket-delete")}</button>
                                </div>
                                <ul className={s.items}>{list}</ul>
                            </div>
                            <div className={s.info}>
                                <h2 className={s.text}>{t("basket-order")}</h2>
                                <div className={s.details}>
                                    <div className={s.item}>
                                        <div className={s.caption}>{t("basket-goods")}:</div>
                                        <div className={s.number}>{props.numberActive} {t("pieces")}</div>
                                    </div>
                                    <div className={s.item}>
                                        <div className={s.caption}>{t("basket-total")}:</div>
                                        <div className={s.cost}>{props.cost}</div>
                                    </div>
                                </div>
                                <Button class="basket" onClick={ () => { props.setTypePopup("buy") } }>{t("buy")}</Button>
                            </div>
                        </div>:
                        <div className={s.wrap}>
                            <div className={s.img}>
                                <img src={empty} alt="empty" />
                            </div>
                            <div className={s.desc}>
                                <h2 className={s.title}>{t("basket-title")}</h2>
                                <h3 className={s.subtitle}>{t("basket-subtitle")}</h3>
                            </div>
                            <NavLink to="/">
                                <Button class="back">{t("to-main")}</Button>
                            </NavLink>
                        </div>
                    }
                </Container>
            </section>
            {props.goods.length != 0 && <Information />}
        </>
    )
}

export default Basket;