import Aside from "./Aside/Aside";

import Container from "../common/Container/Container";
import Information from "../Information/Information";
import Navigation from "../Navigation/Navigation";
import s from "./Seacrch.module.css";
import CardContainer from "../Card/CardContainer";
import { useTranslation } from 'react-i18next';
import Sort from "../common/Sort/Sort";

const Search = (props) => {
    const { t } = useTranslation();
    const goods =  props.goods.map((item, i) => <CardContainer key={i + 1} {...item} isMini={true} />);
    return(
        <>
            <Navigation list={[
                {
                    text: props.listActive,
                    href: `search/${props.hrefActive}`
                },
                {
                    text: props.itemActive,
                    href: `search/${props.hrefActive}`
                }
            ]} />
            <section className={s.search}>
                <Container>
                    <div className={s.wrapper}>
                        <div className={s.info}>
                            <h1 className={s.title}>{props.itemActive}</h1>
                            <Sort />
                        </div>
                        <Aside catalog={props.catalog} />
                        <div className={s.main}>
                            {
                                goods.length !== 0 ? 
                                <ul className={s.list}>{goods}</ul>:
                                <div className={s.text}>{t("nothing")}</div>
                            }
                            {
                                goods.length !== 0 && <button className={s.btn}>{t("show-more")} 20</button>
                            }
                        </div>
                    </div>
                </Container>
            </section>
            <Information />
        </>
    )
}

export default Search;