import { catalog } from "../../api/api";

const SET_CATALOG = "SET-CATALOG/catalog";
const SET_ACTIVE = "SET-ACTIVE/catalog";

const initialState = {
    listActive: "",
    itemActive: "",
    hrefActive: "",
    catalog: []
}

const catalogReducer = (state = initialState, action) => {
    switch(action.type){
        case SET_CATALOG:
            return{
                ...state,
                catalog: [...action.catalog]
            }
        case SET_ACTIVE: 
            return{
                ...state,
                listActive: action.listActive,
                itemActive: action.itemActive,
                hrefActive: action.hrefActive
            }
        default:
            return state;
    }
}

export const setActive = (listActive, itemActive, hrefActive) => ({type: SET_ACTIVE, listActive, itemActive, hrefActive});
export const setCatalog = (catalog) => ({type: SET_CATALOG, catalog});
//ThunkCreator
export const getCatalog = () => (dispatch) => {
    dispatch(setCatalog(catalog.catalog))
}

export const getActive = (selector) => (dispatch) => {
    dispatch(setActive(...catalog.getActive(selector)))
}

export default catalogReducer;