import buy from "../../../assets/img/buy.png";

import s from "./Buy.module.css";
import CanvasContainer from "../../common/Canvas/CanvasContainer";
import { useFormik } from "formik";
import classNames from "classnames";
import { useRef } from "react";
import createMask from "../../../utils/mask/mask";
import { t } from "i18next";
import validate, { isHuman, phoneValidator, presenceSomething } from "../../../utils/validators/validators";

const Buy = (props) => {
    const ref = useRef(null);
    const formik = useFormik({
        initialValues: {
            name: "",
            phone: "+998",
            address: "",
            isHuman: false,
        },
        validate: (values) => {
            return validate(values, {
                name: presenceSomething,
                phone: phoneValidator,
                address: presenceSomething,
                isHuman: isHuman
            })
        },
        onSubmit: (values, {resetForm} ) => {
            alert(JSON.stringify(values, null, 2));
            props.setTypePopup("success");
            resetForm()
        },
    });

    const onClick = (e) => {
        e.preventDefault();
        props.setTypePopup("");
    }
    
    return(
        <CanvasContainer isCenter={true} isShow={props.isShow}>
            <div className={s.wrapper}>
                <div className={s.info}>
                    <div className={s.img}><img src={buy} alt="buy" /></div>
                    <h1 className={s.title}>{t("buy-title")}</h1>
                    <h2 className={s.subtitle}>{t("buy-subtitle")}</h2>
                </div>
                <form className={s.form} onSubmit={formik.handleSubmit}>
                    <div className={s.item}>
                        <input className={s.input} id="name" name="name" type="text" onChange={formik.handleChange} value={formik.values.name} placeholder={t("buy-your-name")} />
                        <label className={s.text} htmlFor="name">{t("buy-name")}</label>
                        {formik.errors.name ? <div className={s.error}>{formik.errors.name}</div> : null}
                    </div>
                    <div className={s.item}>
                        <input className={s.input} id="phone" name="phone" type="text" onChange={formik.handleChange} value={formik.values.phone} ref={ref}
                        onInput={ (e) => { createMask(ref.current, e) } } onFocus={ (e) => { createMask(ref.current, e) } } onBlur={ (e) => { createMask(ref.current, e) } } />
                        <label className={s.text} htmlFor="phone">{t("buy-phone")}</label>
                        {formik.errors.phone ? <div className={s.error}>{formik.errors.phone}</div> : null}
                    </div>
                    <div className={s.item}>
                        <textarea className={s.textarea} id="address" name="address" type="text" onChange={formik.handleChange} value={formik.values.address} placeholder={t("buy-your-address")} />
                        <label className={s.text} htmlFor="address">{t("buy-address")}</label>
                        {formik.errors.address ? <div className={s.error}>{formik.errors.address}</div> : null}
                    </div>
                    <div className={s.wrap}>
                        <input className={s.checkbox} id="isHuman" name="isHuman" type="checkbox" onChange={formik.handleChange} checked={formik.values.isHuman} />
                        <label className={s.label} htmlFor="isHuman">
                            <div className={s.square}></div> 
                            <div className={s.caption}>{t("buy-text")}</div>
                        </label>
                        {formik.errors.isHuman ? <div className={s.error}>{formik.errors.isHuman}</div> : null}
                    </div>
                    <div className={s.btns}>
                        <button onClick={onClick} className={classNames(s.btn, s.gray)}>{t("buy-cancel")}</button>
                        <button type="submit" className={s.btn}>{t("buy-order")}</button>
                    </div>
                </form>
            </div>
        </CanvasContainer>
    )
}

export default Buy;