import logo from "../../assets/icons/logo.svg";
import menu from "../../assets/icons/menu.svg";
import search from "../../assets/icons/search.svg";
import basket from "../../assets/icons/basket.svg";
import heart from "../../assets/icons/heart.svg";
import arrow from "../../assets/icons/arrow/arrow_bottom.svg";
import close from "../../assets/icons/close.svg";

import { Transition } from 'react-transition-group';
import Container from "../common/Container/Container";
import s from "./Header.module.css";
import { NavLink } from "react-router-dom";
import classNames from "classnames";
import { useEffect, useRef, useState } from "react";
import { useTranslation } from 'react-i18next';
import Button from "../common/Button/Button";

const Header = (props) => {
    const item = useRef(null);
    const { t, i18n } = useTranslation();
    const [isActive, setIsActive] = useState(false);
    const toggle = props.typePopup === "catalog";
    
    const onClick = () => {
        if(props.typePopup !== "catalog") return props.setTypePopup("catalog")
        return props.setTypePopup("")
    }

    const changeLanguage = (language) => {
        i18n.changeLanguage(language)
    }

    useEffect(() => {
        window.addEventListener("click", (e) => {
            if(e.target.closest(`.${item.current.classList[0]}`) === null){
                setIsActive(false)
            }
        })
    }, []);

    return(
        <header className={s.header}>
            <Container>
                <div className={s.wrapper}>
                    <div className={s.info}>
                        <NavLink className={s.logo} to="/">
                            <img src={logo} slt="logo" />
                        </NavLink>
                        <Button onClick={onClick} class="header">
                            <Transition in={toggle} timeout={1000}>
                                {state => (
                                    <div className={classNames(s.image, s[state])}>
                                        <span className={s.menu}><img src={menu} alt="menu" /></span>
                                        <span className={s.close}><img src={close} alt="close" /></span>
                                    </div>
                                )}
                            </Transition>
                            <div className={s.text}>{t("catalog")}</div>
                        </Button>
                        <div className={s.search}>
                            <input className={s.input} type="text" id="search" placeholder={t("search")} />
                            <label className={s.label} htmlFor="search">
                                <img src={search} alt="search" />
                            </label>
                        </div>
                    </div>
                    <div className={s.nav}>
                        <NavLink to="/basket" className={s.item}>
                            <div className={s.img}>
                                <img src={basket} alt="basket" />
                                {props.number != 0 && <div className={s.number}>{props.number}</div>}
                            </div>
                            <div className={s.title}>{t("basket")}</div>
                        </NavLink>
                        <NavLink to="/favourite" className={s.item}>
                            <div className={s.img}>
                                <img src={heart} alt="heart" />
                            </div>
                            <div className={s.title}>{t("favorites")}</div>
                        </NavLink>
                        <div ref={item} className={s.wrap}>
                            <button onClick={ () => { setIsActive(!isActive) } } className={classNames(s.btn, {[s.active]: isActive})}>
                                <div className={s.desc}>{t("language")}</div>
                                <div className={s.icon}>
                                    <img src={arrow} alt="arrow" />
                                </div>
                            </button>
                            <ul className={classNames(s.list, {[s.active]: isActive})}>
                                <li>
                                    <button onClick={ () => { changeLanguage("ru") } } className={classNames(s.elem, {[s.active]: t("language") === "Рус"})}>Рус</button>
                                </li>
                                <li>
                                    <button onClick={ () => { changeLanguage("ua") } } className={classNames(s.elem, {[s.active]: t("language") === "Укр"})}>Укр</button>
                                </li>
                                <li>
                                    <button onClick={ () => { changeLanguage("en") } } className={classNames(s.elem, {[s.active]: t("language") === "Eng"})}>Eng</button>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </Container>
        </header>
    )
}

export default Header;