import {phone} from "phone";

export const phoneValidator = value => {
    if(phone(value, {country: 'UZ'}).isValid != true) return "Неправильний номер телефону";
}

export const presenceSomething = value => {
    if(!/\S/.test(value)) return "В цьому полі нічого не" 
}

export const isHuman= value => {
    if(!value) return "Ти робот"
}

const validate = (values, validators)  => {
    let errors = {};

    for (const key in values) {
        if(validators[key]){
            const error = validators[key](values[key]);
            if(typeof(error) === "string"){
                errors = {
                    ...errors,
                    [key]: error 
                }
            }
        }
    }

    return errors;
};

export default validate;