import success from "../../../assets/img/success.png";

import s from "./Success.module.css";
import CanvasContainer from "../../common/Canvas/CanvasContainer";
import { useTranslation } from 'react-i18next';

const Success = (props) => {
    const { t } = useTranslation(); 
    return(
        <CanvasContainer isCenter={true} isShow={props.isShow}>
            <div className={s.wrapper}>
                <div className={s.img}><img src={success} alt="success" /></div>
                <div className={s.info}>
                    <h1 className={s.title}>{t("success-title")}</h1>
                    <h2 className={s.subtitle}>{t("success-subtitle")}</h2>
                </div>
                <button onClick={ () => { props.setTypePopup("") } } className={s.btn}>{t("ok")}</button>
            </div>
        </CanvasContainer>
    )
}

export default Success